<template>
<div>
  <div class="steps-titles">
    针对部分类型数据提供的快速配置，将引导您完成简单的业务设置。您也可以直接编辑过滤语句，实现更复杂的查询要求。
  </div>
  <div>
    <a-descriptions :column="2">
      <a-descriptions-item label="规则名称">
        <div class="account-edit" v-if="nameEdit">
          <a-form-model ref="nameform" :model="nameForm" layout="inline">
            <a-form-model-item prop="name">
              <a-input v-model="nameForm.name"></a-input>
            </a-form-model-item>
          </a-form-model>
          <div class="edit-btn">
            <span @click="canelEdit">保存</span>
            <span @click="closelEdit" style="color: #C91521;padding-left: 15px;">取消</span>
          </div>
        </div>
        <div v-else>
          <span>{{ruleObj.ruleLabel}}</span>
          <a-icon type="edit" style="color: #0456DE;padding-left: 15px;" @click="nameModel"/>
        </div>
      </a-descriptions-item>
      <a-descriptions-item label="规则ID">
        {{ruleObj.id}}
      </a-descriptions-item>
      <a-descriptions-item label="规则描述">
        <div class="account-edit" v-if="descEdit" layout="inline">
          <a-form-model ref="descform" :rules="descrules" :model="descform">
            <a-form-model-item prop="name">
              <BTextarea
                class="full-width"
                style="height: auto"
                v-model="descform.desc"
                placeholder="请输入描述"
              />
            </a-form-model-item>
          </a-form-model>
          <div class="edit-btn">
            <span @click="descCanelEdit" style="padding-left: 15px;">保存</span>
            <span style="color: #C91521;padding-left: 15px;" @click="descCloselEdit">取消</span>
          </div>
        </div>
        <div v-else>
          <span>{{ruleObj.description}}</span>
          <a-icon type="edit" style="color: #0456DE;padding-left: 15px;" @click="descModel"/>
        </div>
      </a-descriptions-item>
      <a-descriptions-item label="数据来源">
        {{ruleObj.subjectResource}}
      </a-descriptions-item>
      <a-descriptions-item label="触发事件">
        {{ruleObj.subjectEvent}}
      </a-descriptions-item>
    </a-descriptions>
  </div>
</div>
</template>

<script>
import BTextarea from '../../../components/BTextarea/BTextarea'
export default {
  name: 'stepsOneDetails',
  props: ['ruleId'],
  components: {
    BTextarea
  },
  data() {
    return {
      descEdit: false,
      nameEdit: false,
      ruleObj: {},
      nameForm: {
        name: ''
      },
      descform: {
        desc: ''
      },
      descrules: {
        desc: [
          { validator: this.$validator.checkRemarkLength, message: '描述最多128字符', trigger: ['blur', 'change'] }
        ]
      }
    }
  },
  created() {
    this.getDetails()
  },
  methods: {
    /**
     * 获取规则详情
     * */
    getDetails() {
      this.$api.getForwardDetails(this.ruleId).then(res => {
        this.ruleObj = res.data
      })
    },
    /**
     * 规则名称修改
     * */
    nameModel() {
      this.nameEdit = true
      Object.assign(this.nameForm, {
        name: this.ruleObj.ruleLabel
      })
    },
    canelEdit() {
      this.nameEdit = false
      this.putInfo()
    },
    closelEdit() {
      this.nameEdit = false
    },
    /**
     * 规则描述修改
     * */
    descModel() {
      this.descEdit = true
      Object.assign(this.descform, {
        desc: this.ruleObj.description
      })
    },
    descCanelEdit() {
      this.descEdit = false
      this.putInfo()
    },
    descCloselEdit() {
      this.descEdit = false
    },
    /**
     * 修改信息
     * */
    putInfo() {
      const obj = {
        id: this.ruleId,
        description: this.descform.desc,
        ruleLabel: this.nameForm.name
      }
      this.$api.putRuleInfo(obj).then(res => {
        this.getDetails()
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.steps-titles {
  font-size: 14px;
  color: #575d6c;
  margin-bottom: 20px;
}
.steps-info {
  display: flex;
  justify-content: space-between;
  .steps-border {
    flex: 1;
  }
  .steps-right {
    flex: 1;
    margin-left: 26px;
  }
}
.account-edit {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  font-weight: 500;
  text-align: left;
  color: #333333;

  .edit-btn {
    width: 150px;
    color: #15C937;
    margin-top: 10px;
    span {
      &:hover {
        color: #7BECD3;
        cursor:pointer;
      }
      &:active {
        color: #7BECD3;
        cursor:pointer;
      }
    }
  }
}
</style>



