<template>
<div>
  <div class="steps-titles">
    完成完整的规则定义后，您就可以控制规则的运行，以实现数据转发。
  </div>
  <div>
    <a-row :gutter="24">
      <a-col :span="6">
        <div class="col-right">
          <img src="../../../assets/images/logo.png" height="56" width="56"/>
          <p>规则未运行</p>
          <a-button @click="changeStatus" v-if="ruleObj.ruleStatus">停止规则</a-button>
          <a-button @click="changeStatus" v-else>启动规则</a-button>
        </div>

      </a-col>
      <a-col :span="18">
        <p>规则概览</p>
        <div class="list-content">
          <div class="list-item">
            <span class="list-item-label">规则名称</span>
            <span class="list-item-content">{{ruleObj.ruleLabel}}</span>
          </div>
          <div class="list-item">
            <span class="list-item-label">规则描述</span>
            <span class="list-item-content">{{ruleObj.description}}</span>
          </div>
          <div class="list-item">
            <span class="list-item-label">数据来源</span>
            <span class="list-item-content" v-for="(item, index) in resourceArr" :key="index" v-if="ruleObj.subjectResource === item.value">
              {{item.label}}
            </span>
          </div>
          <div class="list-item">
            <span class="list-item-label">触发事件</span>
            <span class="list-item-content" v-for="(item, index) in eventArr" :key="index" v-if="ruleObj.subjectEvent === item.value">
              {{item.label}}
            </span>
          </div>
<!--          <div class="list-item">
            <span class="list-item-label">转发目标</span>
            <span class="list-item-content">HTTP</span>
          </div>-->
        </div>
      </a-col>
    </a-row>
  </div>
</div>
</template>

<script>
export default {
  name: 'stepsThree',
  props: ['ruleId'],
  data () {
    return {
      ruleObj: {},
      eventArr: [
        {
          value: 'DEVICE_CREATE',
          label: '设备创建'
        },
        {
          value: 'DEVICE_UPDATE',
          label: '设备更新'
        },
        {
          value: 'DEVICE_DELETE',
          label: '设备删除'
        },
        {
          value: 'DEVICE_PROPERTY_REPORT',
          label: '设备属性上报'
        },
        {
          value: 'DEVICE_MESSAGE_REPORT',
          label: '设备消息上报'
        },
        {
          value: 'DEVICE_STATUS_UPDATE',
          label: '设备状态更改'
        },
        {
          value: 'PRODUCT_CREATE',
          label: '产品创建'
        },
        {
          value: 'PRODUCT_UPDATE,',
          label: '产品更新'
        },
        {
          value: 'PRODUCT_DELETE',
          label: '产品删除'
        }
      ],
      resourceArr: [
        {
          value: 'DEVICE',
          label: '设备'
        },
        {
          value: 'DEVICE_MESSAGE',
          label: '设备消息'
        },
        {
          value: 'DEVICE_PROPERTY',
          label: '设备属性'
        },
        {
          value: 'DEVICE_STATUS',
          label: '设备状态'
        },
        {
          value: 'PRODUCT',
          label: '产品'
        }
      ]
    }
  },
  created() {
    this.getDetails()
  },
  methods: {
    getDetails() {
      this.$api.getForwardDetails(this.ruleId).then(res => {
        console.log('获取规则详情', res)
        this.ruleObj = res.data
      })
    },
    /**
     * 启动/停止规则
     * */
    changeStatus() {
      const obj = {
        forwardRuleId: this.ruleObj.id,
        status: !this.ruleObj.ruleStatus
      }
      this.$api.putForwardStatus(obj).then(res => {
        this.getDetails()
        this.$router.push({path: '/dataForwardingList'})
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.steps-titles {
  font-size: 14px;
  color: #575d6c;
  margin-bottom: 20px;
}
.col-right {
  border-right: 1px dashed #EDF4FF;
  text-align: center;
  padding: 36px 0;
  font-size: 24px;
  p {
    margin-top: 1rem;
  }
}
.list-content {
  .list-item {
    display: flex;
    padding: 8px;
    border-bottom: 1px solid #dfe1e6;
    &:hover {
      background: #f2f5fc;
    }
    .list-item-label {
      flex: 2;
      color: #575d6c;
    }
    .list-item-content {
      flex: 9;
      margin-left: 20px;
    }
  }
}
</style>



