<template>
  <div>
    <div class="home-title">
      <span @click="goBack"><a-icon type="arrow-left" /></span>
      <span>新建数据转发规则详情</span>
    </div>
    <div class="forward-content">
      <a-steps :current="current" @change="onChange">
        <a-step v-for="item in steps" :key="item.title" :title="item.title" />
      </a-steps>
      <div class="steps-content">
        <stepsOneDetails :ruleId="ruleId" v-if="current === 0"></stepsOneDetails>
        <stepsTwo v-if="current === 1" :ruleId="ruleId"></stepsTwo>
        <stepsThree v-if="current === 2" :ruleId="ruleId"></stepsThree>
      </div>
    </div>
  </div>
</template>

<script>
import stepsOneDetails from './steps/stepsOneDetails'
import stepsTwo from './steps/stepsTwo'
import stepsThree from './steps/stepsThree'
export default {
  name: 'dataForwardingDetail',
  components: {
    stepsOneDetails,
    stepsTwo,
    stepsThree
  },
  data () {
    return {
      current: 0,
      steps: [
        {
          title: '设置转发数据',
          subTitle: '针对部分类型数据提供的快速配置，将引导您完成简单的业务设置。您也可以直接编辑过滤语句，实现更复杂的查询要求。',
          content: 'First-content'
        },
        {
          title: '设置转发目标',
          content: 'Second-content'
        },
        {
          title: '启动规则',
          content: 'Last-content'
        }
      ],
      ruleId: ''
    }
  },
  activated() {
    this.ruleId = this.$route.query.id
  },
  created() {
    this.ruleId = this.$route.query.id
  },
  methods: {
    onChange(current) {
      this.current = current
    },
    goBack() {
      this.$router.push('/dataForwardingList')
    }
  }
}
</script>

<style lang="scss" scoped>
.forward-content {
  margin: 24px auto;
  padding: 24px;
  background: #ffffff;
  width: 1200px;
}
.steps-content {
  margin-top: 16px;
  border-radius: 6px;
  background-color: #ffffff;
  min-height: 200px;
  padding-top: 0px;
}
</style>



