          title: 'RabbitMQ',
          key: 'RABBIT'
        },
        {
          title: 'Kafka',
          key: 'KAFKA'
        },
        {
          title: 'HTTP编码',
          key: 'HTTP'
        }
      ],
      arearArr: [
        {
          title: 'cn-east-3',
          value: 1
        }
      ],
      methodArr: [
        {
          title: 'GET',
          value: 'GET'
        },
        {
          title: 'POST',
          value: 'POST'
        },
        {
          title: 'PUT',
          value: 'PUT'
        }
      ],
      typeArr: [
        {
          title: 'DIRECT',
          value: 'DIRECT'
        },
        {
          title: 'FANOUT',
          value: 'FANOUT'
        },
        {
          title: 'TOPIC',
          value: 'TOPIC'
        },
        {
          title: 'HEADERS',
          value: 'HEADERS'
        }
      ],
      resourceList: [],
      listData: [],
      visible: false,
      targetform: {
        forwardRuleId: undefined,
        ruleResourceId: undefined,
        httpMethod: '',
        produceType: undefined,
        produceStrategy: undefined,
        messageKey: undefined,
        topic: '',
        exchange: '',
        exchangeType: undefined,
        routingKey: '',
        exchangeDurable: undefined,
        headers: [],
        resourceType: undefined
      },
      headerArr: [],
      rules: {
        httpMethod: [
          { required: true, message: '请选择请求方式', trigger: 'blur' }
        ],
        ruleResourceId: [
          { required: true, message: '请选择资源', trigger: 'blur' }
        ],
        path: [
          { required: true, message: '请输入path', trigger: 'blur' }
        ]
      },
      isEdit: false,
      titles: '新增转发目标',
      editVisible: false,
      editForm: {
        forwardRuleId: undefined,
        ruleResourceId: undefined,
        httpMethod: '',
        produceType: undefined,
        produceStrategy: undefined,
        messageKey: undefined,
        topic: '',
        exchange: '',
        exchangeType: undefined,
        routingKey: '',
        exchangeDurable: undefined,
        headers: [],
        resourceType: undefined
      }
    }
  },
  created() {
    console.log('规则ID', this.ruleId)
    this.getResList()
  },
  methods: {
    /**
     * 删除
     * */
    deleteCancel(id) {
      this.$api.deleteForwardTarget(id).then(res => {
        this.getResList()
      })
    },
    /**
     *
     * 转发目标列表
     * */
    getResList() {
      const obj = {
        ruleId: this.ruleId
      }
      this.$api.getForwardTargetList(obj).then(res => {
        console.log('资源列表')
        this.listData = res.data
      })
    },
    /**
     * 选择转发目标
     * */
    changeType(val) {
      this.getResourceList()
    },
    /**
     * 资源列表
     * */
    getResourceList() {
      const obj = {
        resourceType: this.targetform.resourceType
      }
      this.$api.getResourceAllList(obj).then(res => {
        console.log('资源列表', res)
        this.resourceList = res.data
      })
    },
    /**
     * HTTP添加请求头
     * */
    addHeader() {
      let obj = {
        key: '',
        value: ''
      }
      this.headerArr.push(obj)
    },
    /**
     * 请求头删除
     * */
    deleteHeader(val) {
      this.headerArr.map((item, index) => {
        if (item === val) {
          this.headerArr.splice(index, 1)
        }
      })
    },
    /**
     * 新增转发目标
     * */
    postAddTarget() {
      let formObj = {
        forwardRuleId: '',
        ruleResourceId: '',
        configuration: {}
      }
      this.encapsulationForm(formObj, this.targetform)
      this.$api.postForwardTarget(formObj).then(res => {
        this.visible = false
        this.getResList()
        this.$emit('creatRule', true)
      })
    },
    showModal() {
      this.isEdit = false
      this.visible = true
      this.targetform = {}
      this.getResourceList()
    },
    handleOk() {
      // if (this.isEdit) {
      //   this.putTarget()
      // } else {
      //   this.postAddTarget()
      // }
      this.postAddTarget()
    },
    /**
     * 修改转发目标接口
     * */
    putTarget() {
      let formObj = {
        forwardRuleId: '',
        ruleResourceId: '',
        configuration: {},
        id: this.editForm.id
      }
      this.encapsulationForm(formObj, this.editForm)
      this.$api.putForwardTarget(formObj).then(res => {
        this.editVisible = false
        this.getResList()
      })
    },
    editHandleOk() {
      this.editVisible = false
      this.putTarget()
    },
    /**
     * 封装资源目标表单
     * */
    encapsulationForm(formObj, targetForm) {
      switch (targetForm.resourceType) {
        case 'HTTP':
          let obj = {}
          this.headerArr.map(items => {
            obj[items.key] = items.value
            console.log('键值对', obj)
          })
          targetForm.headers = obj
          Object.assign(formObj, {
            forwardRuleId: this.ruleId,
            ruleResourceId: targetForm.ruleResourceId,
            configuration: {
              resourceType: targetForm.resourceType,
              httpMethod: targetForm.httpMethod,
              path: targetForm.path,
              headers: targetForm.headers
            }
          })
          break
        case 'RABBIT' :
          Object.assign(formObj, {
            forwardRuleId: this.ruleId,
            ruleResourceId: targetForm.ruleResourceId,
            configuration: {
              resourceType: targetForm.resourceType,
              exchange: targetForm.exchange,
              exchangeType: targetForm.exchangeType,
              routingKey: targetForm.routingKey,
              exchangeDurable: targetForm.exchangeDurable === 0 !== false
            }
          })
          break
        case 'KAFKA':
          Object.assign(formObj, {
            forwardRuleId: this.ruleId,
            ruleResourceId: targetForm.ruleResourceId,
            configuration: {
              topic: targetForm.topic,
              resourceType: targetForm.resourceType,
              produceType: targetForm.produceType,
              produceStrategy: targetForm.produceStrategy,
              messageKey: targetForm.messageKey
            }
          })
          break
      }
      return formObj
    },
    /**
     * 修改转发目标
     * */
    editTarget(item) {
      // this.isEdit = true
      this.editVisible = true
      this.titles = '修改转发目标'
      console.log('修改资源', item)
      // this.editForm.resetFields()
      this.getResourceList()
      if (item.configuration.resourceType === 'HTTP') {
        let arr = []
        let Obj = item.configuration.headers
        for (let propery in Obj) {
          let aObj = {}
          Object.assign(aObj, {
            key: propery,
            value: Obj[propery]
          })
          arr.push(aObj)
        }
        this.headerArr = arr
      }
      console.log('我的值', this.headerArr)
      Object.assign(this.editForm, {
        id: item.id,
        forwardRuleId: item.forwardRuleId,
        ruleResourceId: item.ruleResourceId,
        httpMethod: item.configuration.httpMethod,
        produceType: item.configuration.produceType,
        produceStrategy: item.configuration.produceStrategy,
        messageKey: item.configuration.messageKey,
        topic: item.configuration.topic,
        exchange: item.configuration.exchange,
        exchangeType: item.configuration.exchangeType,
        routingKey: item.configuration.routingKey,
        exchangeDurable: item.configuration.exchangeDurable ? 0 : 1,
        headers: this.headerArr,
        resourceType: item.configuration.resourceType,
        path: item.configuration.path
      })
    },
    produceChange(val) {
      console.log('999', val)
      Object.assign(this.editForm, {
        produceStrategy: val
      })
      // this.editForm.produceStrategy = val
    },
    goForward() {
      this.$router.push('/resourceAllocationList')
    }
  }
}
</script>

<style lang="scss" scoped>
.steps-titles {
  font-size: 14px;
  color: #575d6c;
  margin-bottom: 20px;
}
.steps-target-title {
  margin-bottom: 15px;
}
.list-flex {
  width: 100%;
  display: flex;
  justify-content: space-between;
  .list-subtitle {
    background: #e5dfdf;
  }
}
.select-subtitle {
  margin-left: 7rem;
  margin-top: -1rem;
  margin-bottom: 1.5rem;
}
.form-head {
  display: flex;
  justify-content: space-between;
  background: #d9ddde;
  align-items: center;
  text-align: center;
  .form-items {
    flex: 1;
  }
  .form-add {
    width: 150px;
    color: #0D53BF;
  }
}
.form-ipt {
  width: 100%;
}
</style>



